 casos, la restitución material puede no ser posible. Por ejemplo, si se entregó un bien que ya no existe o se prestó un servicio que no puede deshacerse. En esos escenarios, la Ley permite que se apliquen compensaciones económicas para resarcir el perjuicio causado. Así, quien no pueda devolver lo recibido en especie, deberá hacerlo en dinero, de forma proporcional y justa.")])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"bg-full-width bg-color-2 mb-5"},[_c('div',{staticClass:"px-4 px-md-5 py-4"},[_c('div',{staticClass:"row align-items-center"},[_c('div',{staticClass:"col-lg-auto"},[_c('img',{staticClass:"mx-auto",staticStyle:{"max-width":"90px"},attrs:{"src":require("@/assets/curso/tema3/7.svg")}})]),_c('div',{staticClass:"col-lg"},[_c('p',{staticClass:"mb-0"},[_vm._v("Todo este conjunto de efectos se fundamenta en el principio de que nadie debe enriquecerse injustamente a costa de otro mediante un acto inválido. Por eso, la nulidad no solo elimina el contrato, sino que también impone obligaciones posteriores orientadas a restaurar la situación patrimonial que existía antes de su celebración. Estos efectos, tanto en la nulidad absoluta como en la relativa, son esenciales para garantizar la justicia contractual y la seguridad jurídica en las relaciones civiles.")])])])])])},function () {var _vm=this;var _h=_vm.$createElement;var _c=_vm._self._c||_h;return _c('div',{staticClass:"row mb-5"},[_c('div',{staticClass:"col-lg-4 mb-3 mb-lg-0"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/tema3/8.png"),"data-aos":"zoom-in"}})])]),_c('div',{staticClass:"col-lg-8"},[_c('p',{attrs:{"data-aos":"fade-left"}},[_vm._v("En este punto queda claro que la nulidad cumple una función esencial dentro del sistema jurídico: garantizar la legalidad, proteger los intereses legítimos de las partes, y preservar el orden público. Usted, como futuro abogado, debe aprender a identificar cuándo un contrato puede es